import React, { useState, useRef, useEffect } from 'react';
import axios from 'axios';
import './Chatbot.css';

const Chatbot = () => {
  const [messages, setMessages] = useState([
    {
      id: 1,
      text: "Hola! Soy tu asistente de evaluacin de diseo web. Ingresa la URL de un sitio web y te dar una evaluacin completa con puntaje y recomendaciones.",
      sender: 'bot',
      timestamp: new Date()
    }
  ]);
  const [inputValue, setInputValue] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const messagesEndRef = useRef(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(scrollToBottom, [messages]);

  const validateUrl = (url) => {
    try {
      new URL(url);
      return url.startsWith('http://') || url.startsWith('https://');
    } catch {
      return false;
    }
  };



  const handleSubmit = async (e) => {
    e.preventDefault();
    const url = inputValue.trim();

    if (!url) return;

    if (!validateUrl(url)) {
      setMessages(prev => [...prev, {
        id: Date.now(),
        text: "Por favor ingresa una URL vlida que comience con http:// o https://",
        sender: 'bot',
        timestamp: new Date()
      }]);
      return;
    }

    // Agregar mensaje del usuario
    const userMessage = {
      id: Date.now(),
      text: url,
      sender: 'user',
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setInputValue('');
    setIsLoading(true);

    try {
      // Llamar a la API de evaluacin (el backend maneja la captura y subida de screenshots)
      const apiUrl = process.env.REACT_APP_API_URL || 'http://localhost:8000';
      const response = await axios.post(`${apiUrl}/api/evaluate`, {
        url: url
      }, {
        timeout: 60000 // 60 segundos timeout
      });

      const data = response.data;

      // Usar la URL del screenshot del backend
      const finalScreenshotUrl = data.screenshot_url;

      // Crear mensaje de respuesta
      let responseText = ` **Evaluacin Completa para ${data.url}**\n\n`;
      responseText += ` **Puntaje General:** ${data.total_score}/100 - ${data.grade}\n\n`;

      responseText += ` **Desglose por Categoras:**\n`;
      Object.entries(data.categories).forEach(([category, details]) => {
        const categoryNames = {
          typography: 'Tipografa',
          color: 'Color',
          layout: 'Layout',
          usability: 'Usabilidad'
        };
        responseText += ` ${categoryNames[category] || category}: ${details.score}/100 (${(details.weight * 100).toFixed(0)}%)\n`;
      });

      if (data.recommendations && data.recommendations.length > 0) {
        responseText += `\n **Recomendaciones:**\n`;
        data.recommendations.forEach(rec => {
          responseText += ` ${rec}\n`;
        });
      }

      if (finalScreenshotUrl) {
        responseText += `\n **Screenshot:** [Ver imagen](${finalScreenshotUrl})`;
      }

      const botMessage = {
        id: Date.now() + 1,
        text: responseText,
        sender: 'bot',
        timestamp: new Date(),
        evaluation: { ...data, screenshot_url: finalScreenshotUrl }
      };

      setMessages(prev => [...prev, botMessage]);

    } catch (error) {
      console.error('Error:', error);
      let errorMessage = "Lo siento, ocurri un error al evaluar el sitio web. ";

      if (error.code === 'ECONNABORTED') {
        errorMessage += "La evaluacin tom demasiado tiempo. Intenta con otro sitio.";
      } else if (error.response) {
        errorMessage += error.response.data.detail || "Error del servidor.";
      } else {
        errorMessage += "Verifica que el backend est ejecutndose.";
      }

      setMessages(prev => [...prev, {
        id: Date.now() + 1,
        text: errorMessage,
        sender: 'bot',
        timestamp: new Date()
      }]);
    } finally {
      setIsLoading(false);
    }
  };

  const formatTime = (date) => {
    return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
  };

  return (
    <div className="chatbot-container">
      <div className="chatbot-messages">
        {messages.map(message => (
          <div
            key={message.id}
            className={`message ${message.sender === 'user' ? 'user-message' : 'bot-message'}`}
          >
            <div className="message-content">
              {message.sender === 'bot' ? (
                <div className="message-text">
                  {message.text.split('\n').map((line, index) => {
                    // Detectar si la lnea contiene un enlace de screenshot
                    const screenshotMatch = line.match(/ \*\*Screenshot:\*\* \[Ver imagen\]\((.+)\)/);
                    if (screenshotMatch) {
                      const url = screenshotMatch[1];
                      return (
                        <div key={index}>
                           **Screenshot:**{' '}
                          <a
                            href={url}
                            target="_blank"
                            rel="noopener noreferrer"
                            style={{ color: '#007bff', textDecoration: 'underline', cursor: 'pointer' }}
                          >
                            Ver imagen
                          </a>
                        </div>
                      );
                    }
                    return <div key={index}>{line}</div>;
                  })}
                </div>
              ) : (
                <div className="message-text">{message.text}</div>
              )}
              {message.evaluation && message.evaluation.screenshot_url && (
                <div className="screenshot-container">
                  <img
                    src={message.evaluation.screenshot_url}
                    alt="Screenshot del sitio evaluado"
                    className="screenshot-image"
                    onError={(e) => {
                      e.target.style.display = 'none';
                    }}
                  />
                </div>
              )}
            </div>
            <div className="message-time">{formatTime(message.timestamp)}</div>
          </div>
        ))}

        {isLoading && (
          <div className="message bot-message loading">
            <div className="message-content">
              <div className="typing-indicator">
                <span></span>
                <span></span>
                <span></span>
              </div>
              <div className="loading-text">Evaluando el diseo web...</div>
            </div>
          </div>
        )}

        <div ref={messagesEndRef} />
      </div>

      <form className="chatbot-input-form" onSubmit={handleSubmit}>
        <input
          type="url"
          value={inputValue}
          onChange={(e) => setInputValue(e.target.value)}
          placeholder="Ingresa la URL del sitio web (ej: https://example.com)"
          disabled={isLoading}
          className="chatbot-input"
        />
        <button
          type="submit"
          disabled={isLoading || !inputValue.trim()}
          className="chatbot-submit"
        >
          {isLoading ? 'Evaluando...' : 'Evaluar'}
        </button>
      </form>
    </div>
  );
};

export default Chatbot;